import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, withStyles, useTheme } from '@material-ui/core/styles';
import { alpha } from '@material-ui/core/styles';

import * as ARBApi from '../utils/ARBApi'
import { Grid, Box } from '@material-ui/core';
import PlayerReport from '../components/dialogs/PlayerReport'
import { allPlayers } from '../utils/allPlayers'
import Skeleton from '@material-ui/lab/Skeleton';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import escareRegExp from 'escape-string-regexp'
import sortBy from 'sort-by'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';
import { allRoles, allCountries } from '../utils/generals'
import Moment from 'react-moment';
import 'moment/locale/it';

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: theme.palette.common.white,
        color: theme.palette.common.black,
    },
    body: {
        fontSize: 12,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        },
    }
}))(TableRow);

const useStyles1 = makeStyles((theme) => ({
    root: {
        flexShrink: 0,
        marginLeft: theme.spacing(2.5),
    },
}));

function TablePaginationActions(props) {
    const classes = useStyles1();
    const theme = useTheme();
    const { count, page, rowsPerPage, onPageChange } = props;

    const handleFirstPageButtonClick = (event) => {
        onPageChange(event, 0);
    };

    const handleBackButtonClick = (event) => {
        onPageChange(event, page - 1);
    };

    const handleNextButtonClick = (event) => {
        onPageChange(event, page + 1);
    };

    const handleLastPageButtonClick = (event) => {
        onPageChange(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };

    return (
        <div className={classes.root}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </div>
    );
}

TablePaginationActions.propTypes = {
    count: PropTypes.number.isRequired,
    onPageChange: PropTypes.func.isRequired,
    page: PropTypes.number.isRequired,
    rowsPerPage: PropTypes.number.isRequired,
};

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    container: {
        maxHeight: 540,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'justify',
        color: theme.palette.text.secondary,
        paddingTop: 0,
        marginTop: 0
    },
    box: {
        margin: 0
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: alpha(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: alpha(theme.palette.common.white, 0.25),
        },
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(1),
            width: 'auto',
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: '12ch',
            '&:focus': {
                width: '20ch',
            },
        },
    },
    myTextStyle: {
        textDecoration: 'underline',
        '&:hover': {
            color: '#CA2331'
        },
        color: '#082D68'
    },
    avatar: {
        backgroundColor: '#CA2331',
        color: '#FFFFFF',
    },
    header: {
        fontWeight: 'bold'
    }
}));

export default function App() {
    const classes = useStyles();

    const [openPlayer, setPlayerOpen] = useState(false);
    const [playerId, setPlayerId] = useState(0)
    const [playerReport, setPlayerReport] = useState([]);
    const [query, setQuery] = useState('');
    const [page, setPage] = useState(0);
    const rowsPerPage = 50;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleQuery = (query) => {
        setQuery(query);
        setPage(0);
    };

    const onSelectPlayer = (playerId) => {
        if (playerId > 0) {
            console.log('playerId', playerId);
            ARBApi.getAllByID(`playerDetails`, playerId).then((playerReport) => {
                console.log('playerReport', playerReport);

                if (typeof playerReport !== "undefined") {
                    setPlayerReport(playerReport);
                    setPlayerId(playerId);
                    setPlayerOpen(true);
                }
            });
        }
    }

    const handleClickClosePlayer = () => {
        setPlayerOpen(false);
        setPlayerId(0);
    };

    let filteredPlayers = allPlayers;
    if (query) {
        const searchMatch = new RegExp(escareRegExp(query), 'i');
        filteredPlayers = allPlayers.filter((player) => searchMatch.test(player.name));
        console.log("filteredPlayers", filteredPlayers);
    }
    filteredPlayers.sort(sortBy('name'));

    const getFlag = (flag) => {
        const country = allCountries.filter(country => country.description === flag);
        return country[0].flag;
    }
    if (typeof allPlayers !== 'undefined' && allPlayers.length > 0) {
        allPlayers.map((player) => player.roleabbrevation = allRoles.filter((role) => role.description === player.role), [allRoles]);
    }
    return (
        <Box component="span" m={1} className={classes.box}>
            <Grid container spacing={1}>
                <Grid item xs={12}>
                    {(allPlayers.length > 0) ? (
                        <div className={classes.root}>
                            <AppBar position="static">
                                <Toolbar>
                                    <Typography className={classes.title} variant="h6" noWrap>
                                        Giocatori Cagliari Calcio
                                    </Typography>
                                    <div className={classes.search}>
                                        <div className={classes.searchIcon}>
                                            <SportsSoccerIcon />
                                        </div>
                                        <InputBase
                                            placeholder="Cerca.."
                                            classes={{
                                                root: classes.inputRoot,
                                                input: classes.inputInput,
                                            }}
                                            inputProps={{ 'aria-label': 'search' }}
                                            value={query}
                                            onChange={(event => handleQuery(event.target.value))}
                                        />
                                    </div>
                                </Toolbar>
                            </AppBar>
                            <TableContainer className={classes.container}>
                                <Table stickyHeader size="small" aria-label="a dense table">
                                    <TableHead>
                                        <TableRow>
                                            <StyledTableCell className={classes.header}>Giocatore</StyledTableCell>
                                            <StyledTableCell className={classes.header}>Ruolo</StyledTableCell>
                                            <StyledTableCell className={classes.header}>Data Nascita</StyledTableCell>
                                            <StyledTableCell className={classes.header}>Luogo Nascita</StyledTableCell>
                                            <StyledTableCell className={classes.header} align='center'>Dettagli</StyledTableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {(rowsPerPage > 0
                                            ? filteredPlayers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                            : filteredPlayers
                                        ).map((row) => (
                                            <StyledTableRow key={row.id} onClick={() => onSelectPlayer(row.id)}>
                                                <StyledTableCell>
                                                    <div className='place-avatar' style={{
                                                        backgroundImage: `url(https://www.archiviorossoblu.it/images/nazioni/${getFlag(row.country)})`,
                                                        backgroundRepeat: 'no-repeat',
                                                        backgroundPosition: 'center',
                                                        backgroundPositionX: 0,
                                                        margin: 0,
                                                        paddingLeft: 20,
                                                    }}>
                                                        <Typography variant="subtitle1">{row.surname} {row.name}</Typography>
                                                    </div>
                                                </StyledTableCell>
                                                <StyledTableCell component="th" scope="row">
                                                    <Avatar aria-label="recipe" className={classes.avatar}>{row.roleabbrevation[0].role}</Avatar>
                                                </StyledTableCell>
                                                <StyledTableCell component="th" scope="row">
                                                    <Typography className={classes.myTextStyle} variant="subtitle1" color="textSecondary" onClick={() => onSelectPlayer(row.id)}>
                                                        {(row.date !== '0000-00-00') ? (<Moment locale='it' format="LL">{row.date}</Moment>) : <div>ND</div>}
                                                    </Typography>
                                                </StyledTableCell>
                                                <StyledTableCell component="th" scope="row">
                                                    <Typography className={classes.myTextStyle} variant="subtitle1" color="textSecondary" onClick={() => onSelectPlayer(row.id)}>
                                                        {row.birthplace}
                                                    </Typography>
                                                </StyledTableCell>
                                                <StyledTableCell align='center'>
                                                    <IconButton aria-label="settings" onClick={() => onSelectPlayer(row.id)}>
                                                        <MoreVertIcon />
                                                    </IconButton>
                                                </StyledTableCell>
                                            </StyledTableRow>
                                        ))}
                                    </TableBody>
                                    <TableFooter>
                                        <TableRow>
                                            <TablePagination
                                                rowsPerPageOptions={[10]}
                                                colSpan={10}
                                                count={allPlayers.length}
                                                rowsPerPage={rowsPerPage}
                                                page={page}
                                                SelectProps={{
                                                    inputProps: { 'aria-label': 'rows per page' },
                                                    native: true,
                                                }}
                                                onPageChange={handleChangePage}
                                                ActionsComponent={TablePaginationActions}
                                            />
                                        </TableRow>
                                    </TableFooter>
                                </Table>
                            </TableContainer>
                        </div>
                    ) : (
                        <div>
                            <Skeleton animation="wave" height={40} width="100%" style={{ marginBottom: 6 }} />
                            <Skeleton animation="wave" height={40} width="100%" style={{ marginBottom: 6 }} />
                            <Skeleton animation="wave" height={40} width="100%" style={{ marginBottom: 6 }} />
                            <Skeleton animation="wave" height={40} width="100%" style={{ marginBottom: 6 }} />
                            <Skeleton animation="wave" height={40} width="100%" style={{ marginBottom: 6 }} />
                        </div>
                    )
                    }
                </Grid>
            </Grid>
            {(openPlayer) ? (
                <PlayerReport
                    open={openPlayer}
                    handleClose={handleClickClosePlayer}
                    playerReport={playerReport}
                    playerId={playerId}
                />
            ) : null}
        </Box>
    )
}